html {
    position: relative;
}

a {
    text-decoration: none;
}

.home {
    position: fixed;
}

.section {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.section-row {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.section-row-only {
    position: relative;
    display: flex;
    flex-flow: row wrap;
}

.section-column {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.wrap-row {
    flex-flow: row wrap;
}

.section-static {
    position: relative;
    min-height: 50vh;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.parallax::after {
    content: " ";
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;

    transform: translateZ(-1px) scale(1.5);
    /*background-attachment: fixed;*/
    background-size: cover;
    z-index: -50;
}

.bg1::after {
    background-image: url('../images/home.jpg');
    position: absolute;
    left: -5px;
}

.bg2::after {
    /*background-image: url('../images/about3.jpg');*/
    background-image: url('../images/Liquid-Cheese-3.svg');
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    transform: none;
}

.static {
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 20px 20px 28px 0 rgba(0, 0, 0, 0.24), 28px 20px 38px 0 rgba(0, 0, 0, 0.23);
}

.header {
    font-family: "Berlin Sans FB";
    font-size: calc(20px + 1.5vw);
    font-weight: lighter;
    color: rgb(245, 245, 245);
}

.name {
    margin-top: 20px;
    font-family: "Berlin Sans FB";
    font-size: calc(50px + 3.5vw);
    font-weight: bolder;
    color: white;
}

.submit-button {
    outline: none;
    background-color: #00b6ff;
    border: 1.5px solid #00b6ff;
    color: white;
    font-size: 18px;
    font-family: "Berlin Sans FB";
    font-weight: 500;
    padding: 18px 25px;
    border-radius: 100px;
    -webkit-transition: all 0.2s ease-in-out; /* Safari */
    transition: all 0.2s ease-in-out;
    cursor: pointer;
}

.submit-button:hover {
    background-color: rgba(0, 0, 0, 0);
    border: 1.5px solid lightblue;
    color: white;
}

.submit-button:active {
    background-color: #02a2e2;
    border: 1.5px solid #02a2e2;
    color: white;
}

.submit-button:focus {
    outline: 0;
    box-shadow: none;
}

.button-orange {
    background-color: orange;
    border: 1.5px solid orange;
    color: white;
}

.button-orange:hover {
    background-color: white;
    border: 1.5px solid white;
    color: orange;
}

.button-orange:active {
    background-color: whitesmoke;
    border: 1.5px solid whitesmoke;
    color: orange;
}

.box-shadow {
    box-shadow: 3px 3px 6px 0 rgba(0, 0, 0, 0.2), 9px 4px 18px 0 rgba(0, 0, 0, 0.19);
}

.box-shadow-2 {
    box-shadow: 0 5px 9px 0 rgba(0, 0, 0, 0.22), 0 7px 21px 0 rgba(0, 0, 0, 0.21);
}