html, body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/*******************************************************************/

.wrapper {
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  perspective: 2px;
}


.no-align {
  align-items: unset !important;
}

.align {
  align-items: center !important;
}

.align-center {
  text-align: center !important;
}

.align-left {
  text-align: left;
}

.justify {
  justify-content: center !important;
}

.flex-row {
  display: flex;
  flex-flow: row wrap;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.orange-text {
  color: orange;
}

.blue-text {
  color: #00b7ff;
}

.small-text {
  font-size: 15px;
}

.medium-text {
  font-size: 30px;
  font-weight: bolder;
}

.bold {
  font-weight: 500;
}

.bolder {
  font-weight: bolder;
}

.berlin {
  font-family: "Berlin Sans FB";
}

.forte {
  font-family: "Forte" !important;
}

.arial {
  font-family: Arial, Helvetica, sans-serif;
}

.auto-height {
  min-height: 95vh;
  padding-bottom: 5vh;
  height: auto;
}

.auto-width {
  min-width: 80vw;
  width: auto;
}

.cursor {
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 8px;
  position: absolute;
  background-color: white;
  /*background-image: url("../images/home.jpg");*/
}

::-webkit-scrollbar-thumb {
  background: #00b7ff;
  border-radius: 4px;
  -webkit-transition-duration: 0.3s; /* Safari */
  transition-duration: 0.3s;
}

::-webkit-scrollbar-thumb:active {
  background: #02a3e2;
}

@media only screen and (max-width: 850px) {
  ::-webkit-scrollbar {
    width: 8px;
    position: absolute;
    display: none;
    background-color: white;
    /*background-image: url("../images/home.jpg");*/
  }
  
  ::-webkit-scrollbar-thumb {
    background: #00b7ff;
    border-radius: 4px;
  }
}